.App {
  text-align: center;
  font-family: "Montserrat", sans-serif;
}

h1 {
  font-family: 'Permanent Marker', cursive;
  color: #61b6d3;
  font-size: calc(12px + 4vmin);
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
